import React from 'react';
import { 
  Container, 
  Typography, 
  Box,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button
} from '@mui/material';
import { generatePDFReport } from '../services/reportService';

const History = () => {
  const practiceHistory = JSON.parse(localStorage.getItem('practiceHistory') || '[]');

  const handleDownloadReport = (practice) => {
    generatePDFReport(practice.scores, practice.feedback, practice.response);
  };

  return (
    <Container>
      <Box sx={{ mt: 4 }}>
        <Typography variant="h4" gutterBottom>
          Practice History
        </Typography>
        <TableContainer component={Paper}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Date</TableCell>
                <TableCell>Type</TableCell>
                <TableCell>Fluency Score</TableCell>
                <TableCell>Lexical Score</TableCell>
                <TableCell>Grammar Score</TableCell>
                <TableCell>Pronunciation Score</TableCell>
                <TableCell>Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {practiceHistory.map((practice, index) => (
                <TableRow key={index}>
                  <TableCell>
                    {new Date(practice.date).toLocaleDateString()}
                  </TableCell>
                  <TableCell>{practice.type}</TableCell>
                  <TableCell>{practice.scores.fluency.toFixed(1)}</TableCell>
                  <TableCell>{practice.scores.lexical.toFixed(1)}</TableCell>
                  <TableCell>{practice.scores.grammar.toFixed(1)}</TableCell>
                  <TableCell>{practice.scores.pronunciation.toFixed(1)}</TableCell>
                  <TableCell>
                    <Button 
                      variant="contained" 
                      size="small"
                      onClick={() => handleDownloadReport(practice)}
                    >
                      Download Report
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        {practiceHistory.length === 0 && (
          <Paper sx={{ p: 2, mt: 2 }}>
            <Typography align="center">
              No practice history available yet. Complete some practice sessions to see them here.
            </Typography>
          </Paper>
        )}
      </Box>
    </Container>
  );
};

export default History; 